import {Button, Col, Form, Modal, Row} from "react-bootstrap";
import React, {useEffect, useState} from "react";
import {apiApprovaDocumenti, apiDettaglioPratica, apiInviaEsito, apiInviaDocumentazione} from "../http/api";
import {K} from "../Constants";
import {useQuery} from "../http/helpers";

export const Dettaglio = ({buttonAppDoc,buttonEsito, isProspect, isAllCaricati, update}) => {

  const [pratica, setPratica] = useState({});
  const [showModalConferma, setShowModalConferma] = useState(false);
  function currencyFormatDE(num) {
    return (
      num
        .toFixed(2) // always two decimal digits
        .replace('.', ',') // replace decimal point character with ,
        .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.') + ' €'
    ) // use . as a separator
  }

 const getPratica = (query) => {

    apiDettaglioPratica(K.serviceUrl, query)
      .then(json => {
        setPratica(json)
      })
      .catch(e => console.log("err get pratica"));
  };

  const approvaDocumenti = () => {
    apiApprovaDocumenti(K.serviceUrl, pratica.id)
      .then(json => {
        getPratica(query)
      })
      .catch(e => console.log("err approva"));
  };

  const inviaEsito = () => {
    apiInviaEsito(K.serviceUrl, pratica.id)
      .catch(e => console.log("err invia esito"));
    window.location.reload();
  };

  const inviaDocumentazione = () =>{
      apiInviaDocumentazione(K.serviceUrl, pratica.id)
        .catch(e => console.log("err invio docs"))
      window.location.reload();
  }

  let query = useQuery().get('guid');

  useEffect(() => {
    update();
    getPratica(query);
  }, []);


  return (
    <>


      <Modal show={showModalConferma} onHide={() => setShowModalConferma(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Conferma invio documentazione</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group
              className="mb-3"
              controlId="message">
              <Form.Label>Dopo l'invio della documentazione non sarà più possibile effettuare alcuna modifica,
                sei sicuro di voler continuare?</Form.Label>
            </Form.Group>
          </Form>
        </Modal.Body>

        <Modal.Footer>
          <Button variant="primary" onClick={() => {
            inviaDocumentazione();
            setShowModalConferma(false);
          }}>Conferma</Button>
          <Button variant="danger" onClick={() => {
            setShowModalConferma(false);
          }}>Annulla</Button>
        </Modal.Footer>
      </Modal>


      <div className={'white-rounded-shadowed'}>
        <Row className={'mb-4'}>
          <Col md={12}>
            <p className={'desc-text'}>ANAGRAFICA</p>
          </Col>
        </Row>
        <Row className={'mb-2'}>
          <Col md={10}>
            <p className={'pratica-title'}>{pratica.ragioneSociale}</p>
          </Col>
          <Col md={2}>
            {!isProspect && buttonAppDoc===false && pratica.statoPratica !== "Istruttoria documentale" &&
              <button type="submit" className={"custom-button" + (buttonEsito?"":"-disabled")}  onClick={() => inviaEsito()}>Invia Esito Al Cliente</button>
            }

            {!isProspect && buttonAppDoc===true && pratica.statoPratica === "Controllo documentale" &&
              <button type="submit" className={"custom-button"}  onClick={() => approvaDocumenti()}>Approva Documentazione</button>
            }

            {isProspect && pratica.statoPratica === "Istruttoria documentale" &&
              <button type="submit" className={`custom-button${isAllCaricati?'':'-disabled'}`}  onClick={() => setShowModalConferma(true)}>Invia Documentazione</button>
            }
            </Col>
        </Row>

        <Row className={'margin-custom-b'}>
          <Col md={12}>
            <span className={'desc-text'}>ID</span> <span className={'highlighted-span'}>{pratica.id}</span>
            <span style={{marginLeft: '1rem', marginRight: '1rem', color: '#7F8297'}}>|</span>
            <span className={'desc-text'}>STATO PRATICA</span> <span
            className={'highlighted-span-light'}>{pratica.statoPratica}</span>
          </Col>
        </Row>

        <Row className={'mb-4'}>
          <Col md={5}>
            <span className={'desc-text'}>Nominativo di riferimento</span> <span
            className={'highlighted-span-light'}>{pratica.nominativoRiferimento}</span>
          </Col>
          <Col md={4}>
            <span className={'desc-text'}>Contatto Telefonico</span> <span
            className={'highlighted-span-light'}>{pratica.contattoTelefonico}</span>
          </Col>
          <Col md={3}>
            <span className={'desc-text'}>Importo richiesto</span> <span
            className={'highlighted-span-light'}>{pratica.importoRichiesto?currencyFormatDE(pratica.importoRichiesto): ''}</span>
          </Col>
        </Row>

        <Row className={'mb-4'}>
          <Col md={5}>
            <span className={'desc-text'}>PEC</span> <span className={'highlighted-span-light'}>{pratica.pec}</span>
          </Col>
          <Col md={4}>
            <span className={'desc-text'}>Prodotto</span> <span
            className={'highlighted-span-light'}>{pratica.prodotto}</span>
          </Col>
          <Col md={3}>
            <span className={'desc-text'}>Durata</span> <span
            className={'highlighted-span-light'}>{pratica.durata}</span>
          </Col>
        </Row>

        <Row className={'mb-4'}>
          <Col md={5}>
            <span className={'desc-text'}>Codice Fiscale/P.IVA</span> <span
            className={'highlighted-span-light'}>{pratica.codiceFiscale}</span>
          </Col>
          <Col md={4}>
            <span className={'desc-text'}>Periodicità</span> <span
            className={'highlighted-span-light'}>{pratica.periodicita}</span>
          </Col>
          <Col md={3}>
            <span className={'desc-text'}>Finalità</span> <span
            className={'highlighted-span-light'}>{pratica.finalita}</span>
          </Col>
        </Row>
      </div>
    </>);
}
