export const getDefaultOptions = () => ({
  headers: new Headers({
    'Content-Type': 'application/json'
  })
});


export const request = async (url, options, haveBodyResponse = true) => {
  const response = await fetch(url, options);
  if (response.status === 204) {
    return {};
  }
  if (response.status >= 200 && response.status < 300) {
    if (haveBodyResponse) {
      return response.json();
    }
    return {};
  }
  return Promise.reject(response.status);
};


export const getIsProspect = () => {
  return window.keycloak.token === undefined;
};

export const useQuery = () => {
  return new URLSearchParams(window.location.search);
};


export const getStatoEnum = (stato) => {
  switch (stato) {
    case "In lavorazione":
      return "IN_LAVORAZIONE";
    case "Approvato":
      return "APPROVATO";
    case "Rifiutato":
      return "RIFIUTATO";
    default:
      return null;
  }
}

export const getStatoDesc = (stato) => {
  switch (stato) {
    case "IN_LAVORAZIONE":
      return "In lavorazione";
    case "APPROVATO":
      return "Approvato";
    case "RIFIUTATO":
      return "Rifiutato";
    default:
      return null;
  }

}

