import React, {useEffect, useState} from 'react';
import {Dettaglio} from "./Dettaglio";
import {Documentazione} from "./Documentazione";
import {getIsProspect, useQuery} from "../http/helpers";
import {apiDettaglioDocumenti, apiDettaglioPratica} from "../http/api";
import {K} from "../Constants";
import {useKeycloak} from "@react-keycloak/web";
import {Row} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faArrowLeft } from "@fortawesome/free-solid-svg-icons";

export const Pratica = () => {

  const [appDocActive, setAppDocActive] = useState(false);
  const [esitoActive, setEsitoActive] = useState(false);
  const { keycloak, initialized } = useKeycloak();
  const [statoPratica, setStatoPratica] = useState();
  const [isAllCaricati, setIsAllCaricati] = useState(false)
  const [response, setResponse] = useState([])



  const appDocValid = (value) => {
    setAppDocActive(value);
  };

  const esitoValid = (value) => {
    setEsitoActive(value)
  }

  const changeVisibilitaDocumenti = () => {
    let query = useQuery().get('guid');
    apiDettaglioPratica(K.serviceUrl, query).then(json => {
      console.log("stato pratica:" + json.statoPratica)
      console.log(json.statoPratica === "Istruttoria documentale")
      setStatoPratica(json.statoPratica);
    })
      .catch(e => console.log(e));
  }

  const controllaDocsCaricati = () => {
    let query = useQuery().get('guid');
    apiDettaglioDocumenti(K.serviceUrl, query)
      .then(json => {
        setResponse([...json]);
         setIsAllCaricati(json.filter(x => x.obbligatorio).every(element => {
           return element.listaDocumenti.length > 0 && element.statoDocumento !== "RIFIUTATO";
         }));
      })
      .catch(e => console.log(e));
  };

  const changeDocsCaricati = (isAllCaricati) =>{
    setIsAllCaricati(isAllCaricati);
  }

  useEffect(() => {
    changeVisibilitaDocumenti();
    controllaDocsCaricati();
  }, []);

  return (
    <>
      <div className={'back-col '}>
        {!keycloak.token === undefined &&
          <button  className={'custom-button m-3'} onClick={event =>window.location.href = "/entando-de-app/en/lista_richieste.page"}>
          <FontAwesomeIcon icon={faArrowLeft}/> Torna alla lista
        </button>}
        {keycloak.token === undefined &&
          <button  className={'custom-button m-3'} onClick={event =>window.location.href = "/entando-de-app"}>
            <FontAwesomeIcon icon={faArrowLeft}/> Torna alla home
          </button>}
        <Dettaglio buttonAppDoc={appDocActive} buttonEsito={esitoActive}  update={controllaDocsCaricati} isAllCaricati={isAllCaricati} isProspect={keycloak.token === undefined} />
        <Documentazione isProspect={keycloak.token === undefined} appDocValid={appDocValid} esitoValid={esitoValid} stato={statoPratica}
                        changeDocsCaricati={changeDocsCaricati} update={controllaDocsCaricati}/>
      </div>
    </>);
}


