import {Col, Row, Spinner} from "react-bootstrap";
import React, {useEffect, useState} from 'react';
import {SezioneDocumento} from "./common/SezioneDocumento";
import {K} from "../Constants";
import {apiDettaglioDocumenti, apiDettaglioPratica, apiDownloadDocumentazione} from "../http/api";
import {useQuery} from "../http/helpers";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faDownload} from "@fortawesome/free-solid-svg-icons";


export const Documentazione = ({isProspect, appDocValid, esitoValid, update}) => {

  let query = useQuery().get('guid');
  const [response, setResponse] = useState([]);
  const [statoPratica, setStatoPratica] = useState();
  const [downloading, setDownloading] = useState(false);


  function allAreApprovati(array) {
    return array.filter(y => y.tipoDocumento !== "Altro").filter(x => x.listaDocumenti.length > 0).every(element => {
      return element.statoDocumento === "APPROVATO";
    });
  }

  function allAreNotInLavorazione(array) {
    return array.filter(y => y.tipoDocumento !== "Altro").filter(x => x.listaDocumenti.length > 0).every(element => {
      return (element.statoDocumento === "APPROVATO" || element.statoDocumento === "RIFIUTATO" );
    });
  }

  const getDocumentazione = () => {
    let query = useQuery().get('guid');
    apiDettaglioDocumenti(K.serviceUrl, query)
      .then(json => {
        setResponse([...json]);
        //changeDocsCaricati(allAreCaricati(json));
        appDocValid(allAreApprovati(json));
        esitoValid(allAreNotInLavorazione(json))
        update();
      })
      .catch(e => console.log(e));
  };

  const getStatoPratica = () => {
    apiDettaglioPratica(K.serviceUrl, query).then(json => {
      setStatoPratica(json.statoPratica)
    })
  }



  const downloadAll = () => {
    let query = useQuery().get('guid');
    setTimeout(100000);
    apiDownloadDocumentazione(K.serviceUrl, query, setDownloading).then(() => setDownloading(false))
  };


  useEffect(() => {
    update();
    getDocumentazione();
    getStatoPratica();
  }, []);


  return (
    <>
      {!isProspect && statoPratica === "Istruttoria documentale" &&
        <div className={'white-rounded-shadowed '}>

          <p className={'pratica-title text-center yellow-color'}>In attesa di revisione da parte del cliente</p>
        </div>}

      {isProspect && statoPratica === "Controllo documentale" &&
        <div className={'white-rounded-shadowed mt-4'}>
          <Col md={1}>

            <p className={'desc-text'}>DOCUMENTAZIONE</p>
          </Col>
          <p className={'pratica-title text-center'}>In attesa di revisione da parte dell'operatore</p>
        </div>}

      {isProspect && statoPratica === "Analisi istruttoria creditizia" &&
        <div className={'white-rounded-shadowed'}>
          <p className={'pratica-title text-center green-color'}>Lo stato della sua pratica
            è stato aggiornato in "Analisi Istruttoria Creditizia"</p>
        </div>
        }


      {((isProspect && statoPratica === "Istruttoria documentale") || (!isProspect)) &&
        <>
          {
            K.categorie.map(categoria => {
              return (
                <>
                  <div className={'white-rounded-shadowed mt-4'}>
                    <Row className={'align-to-start'}>
                      <Col md={10}>
                        <p className={'desc-text'}>{categoria.toString().toUpperCase()}</p>
                      </Col>
                      <Col md={2}>
                        <p className={'desc-text'}>*Campi Obbligatori</p>
                      </Col>
                    </Row>
                    {!isProspect &&  statoPratica!== "Istruttoria documentale" &&
                      <Row className={'align-to-end'}>
                        <Col md={13}>

        <span className={'primary-color btn'} onClick={() => {
          downloadAll();
          setDownloading(true);
        }}>Scarica tutto
          {downloading === true ? <Spinner className={'mx-2'} animation="border" variant="primary" size="sm"/>
            : <FontAwesomeIcon icon={faDownload} className={' ms-1 primary-color'}></FontAwesomeIcon>}
         </span>
                        </Col>
                      </Row>}

                    <Row className={'align-to-start mt-4'}>
                      <Col md={5}>
                        <p className={'desc-text margin-custom-l'}>DOCUMENTI {isProspect ? "DA CARICARE" : ""}</p>
                      </Col>
                      <Col md={3}>
                        <p className={'desc-text'}>FILE</p>
                      </Col>
                      <Col md={2}>
                        <p className={'desc-text'}>STATO</p>
                      </Col>
                      <Col md={2}></Col>
                    </Row>
                    {response
                      .filter(sezione => {
                        return sezione.categoria === categoria
                      })
                      .map((value, index) => <SezioneDocumento key={value.tipoDocumento}
                                                               tipoDocumento={value.tipoDocumento}
                                                               classeDocumentoId={value.idClasseDocumento}
                                                               statoDocumento={value.statoDocumento}
                                                               statoPratica={statoPratica}
                                                               listaDoc={value.listaDocumenti}
                                                               notaFromDoc={value.nota}
                                                               isProspect={isProspect}
                                                               update={getDocumentazione}
                                                               required={value.obbligatorio}/>)
                    }


                  </div>
                </>)
            })
          }
        </>
      }
      {((isProspect && statoPratica === "Istruttoria documentale") || (!isProspect)) &&
        <div className={'white-rounded-shadowed mt-4'}>
          <Row className={'align-to-start'}>
            <Col md={12}>
              <p className={'desc-text'}>DOCUMENTAZIONE AGGIUNTIVA</p>
            </Col>
          </Row>

          <Row className={'align-to-start mt-4'}>
            <Col md={10}>
              <p className={'desc-text margin-custom-l'}>CARICA DOCUMENTI</p>
            </Col>

            <Col md={2}></Col>
          </Row>
          {response.filter(value => value.tipoDocumento === "Altro").map((value, index) => <SezioneDocumento
            key={value.tipoDocumento}
            tipoDocumento={value.tipoDocumento}
            classeDocumentoId={value.idClasseDocumento}
            statoDocumento={value.statoDocumento}
            statoPratica={statoPratica}
            nota={value.nota}
            listaDoc={value.listaDocumenti}
            isProspect={isProspect}
            update={getDocumentazione}/>)}

        </div>
      }
    </>);
}
